import React, { Component } from "react";
import '../App.css';
 
class About extends Component {
  render() {
    return (
      <div>
        <h2>About Us</h2>
        <div className='about-para'>Over the period of the last 04 years, YOU have taken Berriesnet from a simple business idea to a revolution in the heart of Pakistan’s Information Technology market. The idea behind the development of Berriesnetwas never about creating a retailer with good prices; but to give birth to a place where people could be served as a part of the family. Where they could actually feel the touch of Information Technology’s application in the way they are dealt with. The world is changing and Computer Zone happily accepts that the customers of today are more tech savvy and demand value for their money. We believe that change is the only thing constant and in order to deliver to you the value you deserve, we are always in the process of evolving to the next best level.
 
        The success of Berriesnet is not a magical coincidence but a result of experienced professionals listening to and implementing every suggestion that YOU gifted us with over the past 04 years. The world has revolutionized and it’s time to move from those close gullied and air tight markets to a system of automation where you do not need to run after sales people asking for product specification and quotations. 
        </div>
      </div>
    );
  }
}
 
export default About;